.help-container {
  display: flex;
  flex-direction: column;
  background-color: #E8F6EF;
  padding-bottom: 10px;
}

.help-nav {
  display: flex;
  margin: 0;
}

.help-category {
  width: 100px;
  text-align: center;
  display: block;
  cursor: pointer;
  background-color: blue;
  padding: 5px;
  border-radius: 8px 8px 0 0;
  font-weight: bold;
}

.help-category:hover {
  backdrop-filter: brightness(60%);
}

.help-content {
  height: 175px;
  overflow-y: scroll;
}

.help-content ul {
  list-style-type: none;
  margin-top: 10px;
}

.help-command {
  display: flex;
  padding: 3px 0;
  font-size: 0.9em;
}

.help-command-title {
  flex: 3;
  font-weight: bold;
}

.help-command-param {
  display: inline;
  text-decoration: underline;
  font-weight: normal;
}

.help-command-description {
  flex: 6;
  align-self: flex-end;
}

.help-command-example {
  flex: 4;
  font-style: italic;
  font-size: 0.8em;
  align-self: flex-end;
}

.Outputs {
  background-color: rgb(18, 181, 99);
}

.Inputs {
  background-color: rgb(153, 102, 255);
}

.Control {
  background-color: rgb(255, 171, 25);
}

.Utils {
  background-color: rgb(92, 177, 214);
}

.Variables {
  background-color: rgb(255, 140, 26);
}

.Operators {
  background-color: rgb(207, 99, 207);
}

.Colors {
  margin-left: 20px;
  color: white;
  background: linear-gradient(
        90deg,
        rgba(255, 0, 0, 1) 0%,
        rgba(255, 154, 0, 1) 10%,
        rgba(208, 222, 33, 1) 20%,
        rgba(79, 220, 74, 1) 30%,
        rgba(63, 218, 216, 1) 40%,
        rgba(47, 201, 226, 1) 50%,
        rgba(28, 127, 238, 1) 60%,
        rgba(95, 21, 242, 1) 70%,
        rgba(186, 12, 248, 1) 80%,
        rgba(251, 7, 217, 1) 90%,
        rgba(255, 0, 0, 1) 100%
    );
}
