@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
html, body {
  min-height: 100%;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100%;
}

.header-frame {
  position: relative;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  background-color: #FFE194;
}

.header-title {
  display: flex;
  align-items: center;
  padding: 0 10px 0 10px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: green;
  font-family: 'Press Start 2P', cursive;
  font-size: 1em;
}

.header-title img {
  height: 60%;
  padding-top: 2px;
  padding-right: 10px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.stretch {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1 1;
}

.header-menu {
  background-color: #FFE194;
  position: absolute;
  top: 30px;
  z-index: 1;
}

.spacer {
  width: 8px;
}

.monitor-frame {
  display: flex;
  margin: 0 5px;
  background-color: rgba(0,0,0,0.1);
  border-radius: 10px;
}

.monitor-label {
  background-color: rgba(0,0,0,0.2);
  border-radius: 20px;
  padding: 2px 7px;
}

.monitor-val {
  padding: 2px 8px 2px 5px;
  width: 30px;
  text-align: center;
}

.icon-button {
  height: 23px;
  width: 23px;
  padding: 3px;
  margin: 3px 2px;
  background-color: rgba(255,255,255,0.20);
  cursor: pointer;
  -webkit-border-radius: 4px;
}

.icon-button.connected {
  background-color: rgba(0,255,0,0.10);
}

.icon-button.spinning {
  animation-name: spin;
  animation-duration: 5000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.icon-button img {
  height: 100%;
  -webkit-filter: grayscale(100%) brightness(1.5);
          filter: grayscale(100%) brightness(1.5);
}

.icon-button.connected img {
  -webkit-filter: hue-rotate(90deg) brightness(1.9);
          filter: hue-rotate(90deg) brightness(1.9);
}

@keyframes spin {
  from {
    transform:rotate(0deg);
  }
  to {
    transform:rotate(360deg);
  }
}

.editor-frame {
  flex-direction: column;
}

.editor-text-frame {
  display: flex;
  flex: 1 1;
}

.editor-text {
  background: white;
  width: 100%;
  font-family: courier;
  font-size: 16px;
  padding: 5px 10px 5px;
  resize: none;
  outline: none !important;
  color: #4c4c4c;
  border:solid 1px #ccc;
  -webkit-border-radius: 4px;
  -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.2);
}

.default {
  color: #939393;
  padding: 20px;
}

.center {
  text-align: center;
}

.editor-container {
  display: flex;
  width: 100%;
  height: 100%;
  background-color: #E8F6EF;
}

.footer-frame {
  padding: 5px;
  background-color: #4C4C6D;
  color: white;
  font-size: 0.8em;
  font-weight: bold;
}

.box-frame {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.25);
}

.box-content {
  min-width: 100px;
  min-height: 50px;
  padding: 10px;
  background-color: white;
  box-shadow: 0 0 10px rgba(0,0,0,0.15);
  border-radius: 10px;
}

.settings-frame {
  margin: 20px 40px;
}

.settings-header {
  font-size: 1.4em;
  font-weight: bold;
  padding-bottom: 20px;
}

.settings-title {
  font-size: 1.1em;
  font-weight: bold;
  padding: 20px 0 5px 0;
  margin-bottom: 5px;
  border-bottom: 1px solid black;
}

.settings-row {
  padding: 2px 0;
}

.settings-device-type {
  margin-left: 10px;
}

.help-container {
  display: flex;
  flex-direction: column;
  background-color: #E8F6EF;
  padding-bottom: 10px;
}

.help-nav {
  display: flex;
  margin: 0;
}

.help-category {
  width: 100px;
  text-align: center;
  display: block;
  cursor: pointer;
  background-color: blue;
  padding: 5px;
  border-radius: 8px 8px 0 0;
  font-weight: bold;
}

.help-category:hover {
  -webkit-backdrop-filter: brightness(60%);
          backdrop-filter: brightness(60%);
}

.help-content {
  height: 175px;
  overflow-y: scroll;
}

.help-content ul {
  list-style-type: none;
  margin-top: 10px;
}

.help-command {
  display: flex;
  padding: 3px 0;
  font-size: 0.9em;
}

.help-command-title {
  flex: 3 1;
  font-weight: bold;
}

.help-command-param {
  display: inline;
  text-decoration: underline;
  font-weight: normal;
}

.help-command-description {
  flex: 6 1;
  align-self: flex-end;
}

.help-command-example {
  flex: 4 1;
  font-style: italic;
  font-size: 0.8em;
  align-self: flex-end;
}

.Outputs {
  background-color: rgb(18, 181, 99);
}

.Inputs {
  background-color: rgb(153, 102, 255);
}

.Control {
  background-color: rgb(255, 171, 25);
}

.Utils {
  background-color: rgb(92, 177, 214);
}

.Variables {
  background-color: rgb(255, 140, 26);
}

.Operators {
  background-color: rgb(207, 99, 207);
}

.Colors {
  margin-left: 20px;
  color: white;
  background: linear-gradient(
        90deg,
        rgba(255, 0, 0, 1) 0%,
        rgba(255, 154, 0, 1) 10%,
        rgba(208, 222, 33, 1) 20%,
        rgba(79, 220, 74, 1) 30%,
        rgba(63, 218, 216, 1) 40%,
        rgba(47, 201, 226, 1) 50%,
        rgba(28, 127, 238, 1) 60%,
        rgba(95, 21, 242, 1) 70%,
        rgba(186, 12, 248, 1) 80%,
        rgba(251, 7, 217, 1) 90%,
        rgba(255, 0, 0, 1) 100%
    );
}

