}

.gui-frame {
  display: flex;
  flex-flow: column;
  height: 100%;
}

.gui-header {
  flex: 0 1 auto;
}

.gui-body {
  flex: 1 1 auto;
}

.gui-footer {
  flex: 0 1 20px;
}

